.movie {
  display: flex;
  border-bottom: 1px solid grey;
  padding-bottom: 30px;
}

.movie_detail {
  padding-left: 30px;
}

.genres {
  display: flex;
  list-style: none;
  gap: 5px;
  padding-top: 5px;
}

.add_info {
  padding-top: 30px;
}

.movie_image {
  border: 1px solid #eee;
  box-shadow: 0px 0px 5px 2px #bfbfbf;
}

.movie_image img {
  object-fit: cover;
  display: block;
}

.add_infoList {
  display: flex;
  gap: 10px;
  padding: 15px 0;
  list-style: none;
}

.add_info a.active {
  border: 1px solid #eee;
}

.add_infoLink.active {
  border: 1px solid #eee;
}

.goBack {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 5px;
  margin: 0px 0px 15px;
  width: 90px;
  border: 1px solid;
  padding: 5px;
  background-color: #0000ee;
  color: white;
  border-radius: 4px;
}
